











import Vue from 'vue'

export default Vue.extend({
  name: 'TextArea',
  props: {
    type: String,
    placeholder: String
  },
  data () {
    return {
      value: ''
    }
  },
  watch: {
    value: function (value) {
      this.$store.dispatch('UPDATE_TEXTAREA_VALUE', value)
    }
  },
  methods: {
    submit () {
      if (this.type === 'textareaFirst') {
        this.$store.dispatch('UPDATE_SUBMIT_FLAG', 1)
      } else {
        this.$store.dispatch('UPDATE_SUBMIT_FLAG', 2)
      }
    }
  }
})
