












import Vue from 'vue'

export default Vue.extend({
  name: 'Input',
  props: {
    type: String,
    label: String
  },
  data () {
    return {
      value: ''
    }
  },
  watch: {
    value: function (value) {
      const input: object = { value, type: this.type }
      this.$store.dispatch('UPDATE_INPUT_VALUE', input)
    }
  }
})
